}


.card-container {
    text-align: center;
    width: 30%;
    margin: 1% 1% 1.5% 1%;
    border: 1px solid rgb(201, 199, 199);
    border-radius: 5px;
    background: white;
}

.card-container:hover {
    box-shadow: 8px 15px 15px -10px rgba(66,66,66,1);
}

.card-container > a > section > img {
    width: 95%;
    height: 140px;
    margin: 2% 0;   
}

.card-container > a {
    text-decoration: none;
    color: black;
}

#bottom-event-card-section {
    display: flex;
    margin: 0 3%;
}

