    color: #ed4c4c;
    padding-top: 13%;
    padding-left: 8%;
}

#event-card-right-section {
    text-align: left;
    display: flex;
    flex-direction: column;
    width: 75%;
    margin-top: -6%;
}

#venue-name-and-likes {
    display: flex;
    justify-content: space-between;
